import http from '../../http-common'

class BranchsSercice {
  getAll () {
    return http.get('Branchs')
  }

  getAllPagnation (params) {
    return http.get('BranchsPaging', { params })
  }

  get (id) {
    return http.get(`Branchs?Id=${id}`)
  }

  create (data) {
    return http.post('Branchs', data)
  }

  update (id, data) {
    return http.put(`Branchs?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Branchs?Id=${id}`)
  }
}

export default new BranchsSercice()
