<template>
  <va-card :title="$t('tables.labelSand')" class="w-100">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="mr-0"
      @click="showCreateModal(1)">
      {{ $t('buttons.sandRecive') }}
    </va-button>
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{props.rowIndex + 1}}
      </template>
      <template slot="date" slot-scope="props">
        {{ formatDate(props.rowData.date)}}
      </template>
      <template slot="name_Sand_Type" slot-scope="props">
        {{ props.rowData.sand_Type.name}}
      </template>
      <template slot="name_Pay_Type" slot-scope="props">
        {{ props.rowData.pay_Type.name}}
      </template>
      <template slot="name_Branchs" slot-scope="props">
        {{ props.rowData.branchs.name}}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>

        <va-button flat small color="danger" @click="showDeleteModal(props.rowData)" class="ma-0">
          {{ $t('tables.delete') }}
        </va-button>
        <va-button flat small color="info" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.view') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefun()"
      @cancel="cancleDeletefun()">
    </va-modal>

    <va-modal v-model="createModal" no-outside-dismiss
      size="large"
      :title="$t(modelTitle)"
      hide-default-actions >
      <form @submit.prevent="editModal? okEditfun()  :okCreatefun()">
        <div class="row flex-center">

          <div class="flex md6 sm12 xs12 px-1">
            <va-date-picker
              :label="$t('placeholder.date')"
              v-model="currentobject.date"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.value')}} </label>
            <va-input v-model="currentobject.value" type="number" step="1"
              :error="numircriquierdvalidation(currentobject.value)"
              :error-messages="$t('errorMessage.Number')"
              :placeholder="$t('placeholder.value')" required/>
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.customers')}} </label>
            <va-select
              :label="$t('placeholder.customers')"
              v-model="fKCustomers.value"
              searchable
              textBy="name"
              keyBy="id"
              :options="fKCustomers.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.branchs')}} </label>
            <va-select
              :label="$t('placeholder.branchs')"
              v-model="fKBranchs.value"
              searchable
              textBy="name"
              keyBy="id"
              :options="fKBranchs.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.fromAccounts')}} </label>
            <va-select
              :label="$t('placeholder.fromAccounts')"
              v-model="fKFromAccounts.value"
              :error="riquierdvalidation(fKFromAccounts.value)"
              :error-messages="['error message']"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fKFromAccounts.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.toAccounts')}} </label>
            <va-select
              :label="$t('placeholder.toAccounts')"
              v-model="fKToAccounts.value"
              :error="riquierdvalidation(fKToAccounts.value)"
              :error-messages="['error message']"
              searchable
              textBy="name"
              keyBy="id"
              :options="fKToAccounts.options"
            />
          </div>
          <va-checkbox
            :label="$t('placeholder.payByCheck')"
            v-model="currentobject.Pay_Type"
          />
        </div>

        <div class="row flex-center" v-if="currentobject.Pay_Type">
          <div class="flex md6 sm12 xs12 px-1">
            <va-input v-model="currentobject.createPay_CheckRequest.check_No"
              :error="riquierdvalidation(currentobject.createPay_CheckRequest.check_No)"
              :error-messages="$t('errorMessage.required')"
              :placeholder="$t('placeholder.check_No')" required/>
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-date-picker
              :label="$t('placeholder.check_Date')"
              v-model="currentobject.createPay_CheckRequest.check_Date"
            />
          </div>
          <div class="flex md12 sm12 xs12 px-1">
            <va-input v-model="currentobject.createPay_CheckRequest.notes"
              :placeholder="$t('placeholder.notes')" type="textarea"/>
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-select
              :label="$t('placeholder.banks')"
              v-model="fKBanks.value"
              searchable
              textBy="name"
              keyBy="id"
              :options="fKBanks.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-select
              :label="$t('placeholder.checkState')"
              v-model="fKCheckState.value"
              searchable
              textBy="name"
              keyBy="id"
              :options="fKCheckState.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-select
              :label="$t('placeholder.customers')"
              v-model="fKCustomers.valueCheck"
              searchable
              textBy="name"
              keyBy="id"
              :options="fKCustomers.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <va-select
              :label="$t('placeholder.branchs')"
              v-model="fKBranchs.valueCheck"
              searchable
              textBy="name"
              keyBy="id"
              :options="fKBranchs.options"
            />
          </div>
        </div>
        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="cancleCreatefun()">
            {{ $t('modal.cancel') }}        </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktext) }}</va-button>
        </div>
      </form>

    </va-modal>
  </va-card>

</template>

<script>
import SandService from '../../../../services/finances/SandService'
import AccountsService from '../../../../services/finances/AccountsService'
import BanksService from '../../../../services/BasicData/BanksService'
import BranchsService from '../../../../services/BasicData/BranchsService'
import CustomersService from '../../../../services/BasicData/CustomersService'
import CheckStateService from '../../../../services/BasicData/CheckStateService'
import helper from '../../../../helper/helper'

export default {
  data () {
    return {
      modelTitle: '',
      modelOktext: '',
      stopBtn: false,
      is_view: false,
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      deleteModal: false,
      editModal: false,
      createModal: false,
      loading: true,
      opjectBeforeEdit: {},
      currentobject: {
        date: '',
        Pay_Type: false,
        createPay_CheckRequest: {},
      },
      fKCustomers: {
        value: '',
        valueCheck: '',
        options: [],
      },
      fKBranchs: {
        value: '',
        valueCheck: '',
        options: [],
      },
      fKFromAccounts: {
        value: '',
        options: [],
      },
      fKToAccounts: {
        value: '',
        options: [],
      },
      fKBanks: {
        value: '',
        options: [],
      },
      fKCheckState: {
        value: '',
        options: [],
      },
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: 'sand_id',
        title: this.$t('tables.headings.sand_id'),
      },
      {
        name: 'value',
        title: this.$t('tables.headings.value'),
      },
      {
        name: '__slot:date',
        title: this.$t('tables.headings.date'),
      },
      {
        name: '__slot:name_Sand_Type',
        title: this.$t('tables.headings.name_Sand_Type'),
      },
      {
        name: '__slot:name_Pay_Type',
        title: this.$t('tables.headings.name_Pay_Type'),
      },
      {
        name: '__slot:name_Branchs',
        title: this.$t('tables.headings.name_Branchs'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  created () {
    this.readItems()
    this.getfKBanks()
    this.getAccounts()
    this.getfKBranchs()
    this.getfKCustomers()
    this.getfKCheckState()
  },

  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    numircvalidation (data) {
      return helper.numircvalidation(data)
    },
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },
    formatDate (dateString, FullDate = true) {
      if (FullDate) {
        const options = { weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      } else {
        const options = { year: 'numeric', month: 'numeric', day: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      }
    },

    /*  api Padination */
    readItems (page = 1) {
      this.loading = true
      console.log('fkghg')
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        Sand_Type: 1,
      }
      SandService.getAllPagnation(params)
        .then(response => {
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getfKCustomers () {
      CustomersService.getAll()
        .then(response => {
          this.fKCustomers.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getfKBranchs () {
      BranchsService.getAll()
        .then(response => {
          this.fKBranchs.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getAccounts () {
      AccountsService.getAll()
        .then(response => {
          this.fKToAccounts.options = response.data
          this.fKFromAccounts.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getfKBanks () {
      BanksService.getAll()
        .then(response => {
          this.fKBanks.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    getfKCheckState () {
      CheckStateService.getAll()
        .then(response => {
          this.fKCheckState.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    /* END API */

    create () {
      if (this.fKToAccounts.value === '' || this.fKFromAccounts.value === '' || this.currentobject.value === null || this.fKToAccounts.value === null || this.fKFromAccounts.value === null || this.currentobject.value === '') {
        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',

        })
      }
      if (this.fKCustomers.value !== '') {
        this.currentobject.fK_Customers = this.fKCustomers.value.id
      }
      if (this.fKBranchs.value !== '') {
        this.currentobject.fK_Branchs = this.fKBranchs.value.id
      }
      if (this.fKFromAccounts.value !== '') {
        this.currentobject.fK_FromAccounts = this.fKFromAccounts.value.id
      }
      if (this.fKToAccounts.value !== '') {
        this.currentobject.fK_ToAccounts = this.fKToAccounts.value.id
      }
      if (this.currentobject.Pay_Type) {
        if (this.fKBanks.value !== '') {
          this.currentobject.createPay_CheckRequest.fK_Banks = this.fKBanks.value.id
        }
        if (this.fKCheckState.value !== '') {
          this.currentobject.createPay_CheckRequest.fK_Check_State = this.fKCheckState.value.id
        }
        if (this.fKBranchs.valueCheck !== '') {
          this.currentobject.createPay_CheckRequest.fK_Branchs = this.fKBranchs.valueCheck.id
        }
        if (this.fKCustomers.valueCheck !== '') {
          this.currentobject.createPay_CheckRequest.fK_Customers = this.fKCustomers.valueCheck.id
        }
        this.currentobject.createPay_CheckRequest.fK_Employees = 1
        this.currentobject.fK_Pay_Type = 2
      }
      if (!this.currentobject.Pay_Type) {
        this.currentobject.createPay_CheckRequest = null
        this.currentobject.fK_Pay_Type = 1
      }
      this.currentobject.fK_Employees = 1
      SandService.create(this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.createModal = false
          this.loading = false
          this.currentobject = {
            date: '',
            Pay_Type: false,
            createPay_CheckRequest: {},
          }
          this.fKCheckState.value = ''
          this.fKBanks.value = ''
          this.fKToAccounts.value = ''
          this.fKFromAccounts.value = ''
          this.fKBranchs.value = ''
          this.fKCustomers.value = ''
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    edit (user) {
      if (this.fKToAccounts.value === '' || this.fKFromAccounts.value === '' || this.currentobject.value === null || this.fKToAccounts.value === null || this.fKFromAccounts.value === null || this.currentobject.value === '') {
        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',

        })
      }
      if (this.fKCustomers.value !== '') {
        this.currentobject.fK_Customers = this.fKCustomers.value.id
      }
      if (this.fKBranchs.value !== '') {
        this.currentobject.fK_Branchs = this.fKBranchs.value.id
      }
      if (this.fKFromAccounts.value !== '') {
        this.currentobject.fK_FromAccounts = this.fKFromAccounts.value.id
      }
      if (this.fKToAccounts.value !== '') {
        this.currentobject.fK_ToAccounts = this.fKToAccounts.value.id
      }
      if (this.currentobject.Pay_Type) {
        if (this.fKBanks.value !== '') {
          this.currentobject.createPay_CheckRequest.fK_Banks = this.fKBanks.value.id
        }
        if (this.fKCheckState.value !== '') {
          this.currentobject.createPay_CheckRequest.fK_Check_State = this.fKCheckState.value.id
        }
        if (this.fKBranchs.valueCheck !== '') {
          this.currentobject.createPay_CheckRequest.fK_Branchs = this.fKBranchs.valueCheck.id
        }
        if (this.fKCustomers.valueCheck !== '') {
          this.currentobject.createPay_CheckRequest.fK_Customers = this.fKCustomers.valueCheck.id
        }
        this.currentobject.fK_Pay_Type = 2
      }
      if (!this.currentobject.Pay_Type) {
        this.currentobject.createPay_CheckRequest = null
        this.currentobject.fK_Pay_Type = 1
      }
      this.currentobject.fK_Employees = 1

      console.log(this.currentobject)
      SandService.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.createModal = false
          this.editModal = false
          this.loading = false
          this.currentobject = {
            date: '',
            Pay_Type: false,
            createPay_CheckRequest: {},
          }
          this.fKCheckState.value = ''
          this.fKBanks.value = ''
          this.fKToAccounts.value = ''
          this.fKFromAccounts.value = ''
          this.fKBranchs.value = ''
          this.fKCustomers.value = ''
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.createModal = false
          this.editModal = false
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },
    remove (id) {
      SandService.delete(id).then(response => {
        if (response.headers['x-status'] === 'true') {
          const idx = this.dataTable.findIndex(u => u.id === id)
          this.dataTable.splice(idx, 1)
          this.loading = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 1500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      })
    },

    /* Delete model */
    showDeleteModal (cuobject) {
      this.deleteModal = true
      this.currentobject = cuobject
      this.loading = true
    },
    okDeletefun () {
      this.remove(this.currentobject.id)
    },
    cancleDeletefun () {
      this.loading = false
    },
    /* end Delete model */

    /* edit model */
    showEditModal (cuobject) {
      if (cuobject.createPay_CheckRequest !== null) {
        cuobject.Pay_Type = true
        this.fKCheckState.value = cuobject.createPay_CheckRequest.check_State
        this.fKBanks.value = cuobject.createPay_CheckRequest.banks
        this.fKBranchs.valueCheck = cuobject.createPay_CheckRequest.branchs
        this.fKCustomers.valueCheck = cuobject.createPay_CheckRequest.customers
      }
      this.editModal = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      this.fKToAccounts.value = cuobject.toAccounts
      this.fKFromAccounts.value = cuobject.fromAccounts
      this.fKBranchs.value = cuobject.branchs
      this.fKCustomers.value = cuobject.customers
      this.createModal = true
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.loading = true
    },
    okEditfun () {
      this.stopBtn = true
      this.edit(this.currentobject)
    },
    cancleEditfun () {
      this.editModal = false
      this.loading = false
      this.createModal = false
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.currentobject = {
        date: '',
        Pay_Type: false,
        createPay_CheckRequest: {},
      }
      this.fKCheckState.value = ''
      this.fKBanks.value = ''
      this.fKToAccounts.value = ''
      this.fKFromAccounts.value = ''
      this.fKBranchs.value = ''
      this.fKCustomers.value = ''
    },
    /* end edit model */

    /* create model */
    showCreateModal (sandType) {
      this.currentobject.fK_Sand_Type = sandType
      this.currentobject.date = helper.todayDate()
      this.modelTitle = 'generic.creatItemTitle'
      this.modelOktext = 'modal.create'
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.stopBtn = true
      this.create()
    },
    cancleCreatefun () {
      this.createModal = false

      this.loading = false
      this.currentobject = {
        date: '',
        Pay_Type: false,
        createPay_CheckRequest: {},
      }
      this.fKCheckState.value = ''
      this.fKBanks.value = ''
      this.fKToAccounts.value = ''
      this.fKFromAccounts.value = ''
      this.fKBranchs.value = ''
      this.fKCustomers.value = ''
    },
    /* end create model */

  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

</style>
