import http from '../../http-common'

class CustomersSercice {
  getAll () {
    return http.get('Customers')
  }

  getAllPagnation (params) {
    return http.get('CustomersPaging', { params })
  }

  get (id) {
    return http.get(`Customers?Id=${id}`)
  }

  create (data) {
    return http.post('Customers', data)
  }

  update (id, data) {
    return http.put(`Customers?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Customers?Id=${id}`)
  }
}

export default new CustomersSercice()
