import http from '../../http-common'

class SandService {
  getAll () {
    return http.get('Sand')
  }

  getAllPagnation (params) {
    return http.get('Sand', { params })
  }

  get (id) {
    return http.get(`Sand?Id=${id}`)
  }

  create (data) {
    return http.post('Sand', data)
  }

  update (id, data) {
    return http.put(`Sand?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Sand?Id=${id}`)
  }
}

export default new SandService()
