import http from '../../http-common'

class CheckStateSercice {
  getAll () {
    return http.get('CheckState')
  }

  getAllPagnation (params) {
    return http.get('CheckStatePaging', { params })
  }

  get (id) {
    return http.get(`CheckState?Id=${id}`)
  }

  create (data) {
    return http.post('CheckState', data)
  }

  update (id, data) {
    return http.put(`CheckState?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`CheckState?Id=${id}`)
  }
}

export default new CheckStateSercice()
